import * as React from 'react';
import ModelView from './ModelView';

const modelViewMountNode = document.body;

export interface SeeseeProps {
  open: boolean;
}

export default class Seesee extends React.PureComponent<SeeseeProps> {

  childrenElement: HTMLElement;

  public render() {
    const {
      children: childrenProps,
      open
    } = this.props;
    React.Children.only(childrenProps);
    const children = React.Children.map(childrenProps, (child) => {
      if (!React.isValidElement(childrenProps)) {
        return null;
      }
      return React.cloneElement(child as React.ReactElement<any>, {
        ref: (el: HTMLElement) => {
          this.childrenElement = el;
        }
      });
    });
    const content = open ? (
    <ModelView mountNode={modelViewMountNode}>
      {children}
    </ModelView>
    ) : children;
    return (content);
  }
}



