import * as React from 'react';
import * as ReactDOM from 'react-dom';
import ViewerLayout from './ViewerLayout';

export interface ModelViewProps {
  /**
   * The element you want mount to.
   * Viewer will be injected into it.
   *
   * @type {HTMLElement}
   */
  mountNode: HTMLElement;
}

/**
 * Modal image viewer. Full page.
 *
 * @class
 */
export default class ModelView extends React.PureComponent<ModelViewProps> {
  public render() {
    const {
      children,
      mountNode
    } = this.props;
    return ReactDOM.createPortal(
      <ViewerLayout
        style={styles.fullPage}
        bg={<span style={styles.bg} />}
      >
        {children}
      </ViewerLayout>,
      mountNode
    );
  }
}

const styles: {[key: string]: React.CSSProperties} = {
  fullPage: {
    position: 'fixed',
    zIndex: 1000,
    width: '100%',
    height: '100%',
    top: '0px',
    left: '0px'
  },
  bg: {
    display: 'block',
    height: '100%',
    width: '100%',
    background: '#000'
  }
};



